import React, { useEffect, useRef, useState } from 'react';
import Editor from '@monaco-editor/react';
import ACTIONS from '../Actions';
import axios from 'axios';
import Button from 'react-bootstrap/esm/Button';
import Dropdown from 'react-bootstrap/Dropdown';

const CodeEditor = ({ socketRef, roomId, onCodeChange }) => {
    const editorRef = useRef(null);
    const [runcode, setRuncode] = useState('');
    const [result, setResult] = useState('');
    const [language, setLanguage] = useState('python');
    const [extension, setExtension] = useState('py');
    const [monacoLanguage, setMonacoLanguage] = useState('javascript');

    //Compiling code
    const run = () => {
        axios
            .post(`http://localhost:5000/${language}`, { runcode })
            .then(
                ({ data }) => {
                    setResult(data);
                }
            );
    }

    const handleEditorDidMount = (editor, monaco) => {
        editorRef.current = editor;
        
        editor.onDidChangeModelContent(() => {
            const code = editor.getValue();
            onCodeChange(code);
            setRuncode(code);

            socketRef.current.emit(ACTIONS.CODE_CHANGE, {
                roomId,
                code,
            });
        });
    };

    useEffect(() => {
        if (socketRef.current) {
            socketRef.current.on(ACTIONS.CODE_CHANGE, ({ code }) => {
                if (code !== null && editorRef.current) {
                    editorRef.current.setValue(code);
                }
            });
        }

        return () => {
            if (socketRef.current) {
                socketRef.current.off(ACTIONS.CODE_CHANGE);
            }
        };
    }, [socketRef.current]);

    //Setting language and extension for various code languages
    const python = () => {
        setLanguage('python');
        setExtension('py');
        setMonacoLanguage('python');
    };
    const node = () => {
        setLanguage('node');
        setExtension('js');
        setMonacoLanguage('javascript');
    }
    const c = () => {
        setLanguage('c');
        setExtension('c');
        setMonacoLanguage('c');
    }
    const cpp = () => {
        setLanguage('cpp');
        setExtension('cpp');
        setMonacoLanguage('cpp');
    }
    const java = () => {
        setLanguage('java');
        setExtension('java');
        setMonacoLanguage('java');
    }

    //Download code file
    const downloadTxtFile = () => {
        const element = document.createElement("a");
        console.log(runcode);
        const file = new Blob([`${runcode}`], {
            type: "text/plain"
        });

        element.href = URL.createObjectURL(file);
        element.download = `Code-Collab.${extension}`;
        document.body.appendChild(element);
        element.click();
    };

    return (
        <>
            <div style={{ height: '400px', border: '1px solid #ccc' }}>
                <Editor
                    height="100%"
                    defaultLanguage="javascript"
                    language={monacoLanguage}
                    theme="vs-dark"
                    onMount={handleEditorDidMount}
                    options={{
                        minimap: { enabled: false },
                        fontSize: 14,
                        wordWrap: 'on',
                        automaticLayout: true,
                        scrollBeyondLastLine: false,
                        lineNumbers: 'on',
                        roundedSelection: false,
                        readOnly: false,
                        cursorStyle: 'line',
                        automaticLayout: true,
                        scrollBeyondLastLine: false,
                        folding: true,
                        foldingStrategy: 'indentation',
                        showFoldingControls: 'always',
                        disableLayerHinting: true,
                        renderLineHighlight: 'all',
                        selectOnLineNumbers: true,
                        glyphMargin: true,
                        useTabStops: false,
                        fontSize: 14,
                        tabSize: 2,
                        insertSpaces: true,
                        wordWrap: 'on',
                        minimap: {
                            enabled: false
                        }
                    }}
                />
            </div>
            <div className='editor-buttons'>
                <Button className='all-btn' style={{ marginRight: '1rem', backgroundColor: 'rgb(77, 103, 195)', border: 'none' }} onClick={downloadTxtFile}>Save</Button>
                <Dropdown>
                    <Dropdown.Toggle className='all-btn' style={{ backgroundColor: '#4d67c3', border: 'none', borderRadius: '7px' }} id="dropdown-basic">
                        {language}
                    </Dropdown.Toggle>

                    <Dropdown.Menu>
                        <Dropdown.Item onClick={node}>javascript</Dropdown.Item>
                        <Dropdown.Item onClick={python}>python</Dropdown.Item>
                        <Dropdown.Item onClick={c}>c</Dropdown.Item>
                        <Dropdown.Item onClick={cpp}>cpp</Dropdown.Item>
                        <Dropdown.Item onClick={java}>java</Dropdown.Item>
                    </Dropdown.Menu>
                </Dropdown>
                <Button onClick={run} className='runbutton' style={{ backgroundColor: 'rgb(231 11 56 / 78%)', border: 'none' }}>Run</Button>
            </div>
            <h5 style={{ fontFamily: '\'Baloo Bhaijaan 2\' , cursive', color: 'white', margin: '1rem' }}>OUTPUT</h5>
            <p style={{ color: 'white', fontFamily: '\'Baloo Bhaijaan 2\' , cursive', color: 'white', margin: '4px', fontWeight: '200', paddingLeft: '1rem' }}>{result}</p>
        </>
    )
};

export default CodeEditor;